        font-size: small;
        margin-bottom: 0;
    }
    menu>hr {
        margin: 0 0 0 -10px;
    }
    menu>li .icon {
        width: 20px;
        vertical-align: middle;
        line-height: normal;
    }
    menu>li img, menu>li .icon {
        margin-right: 10px;
    }
    menu>li a {
        display: flex;
        white-space: nowrap;
        height: 2em;
        line-height: 2em; /* only useful for the .coin */
        align-items: center;
    }
    .coin {
        font-family: ui-monospace, 'Cascadia Code', 'Source Code Pro', Menlo, Consolas, 'DejaVu Sans Mono', monospace;
        cursor: pointer;
        transform-style: preserve-3d;
        transform-box: fill-box; /* for Safari */
        width: 4ch;
        height: 100%;
    }
    .side {
        width: 100%;
        height: 100%;
        position: absolute;
        backface-visibility: hidden;
        display: block;
    }
    .head {
        z-index: 10;
    }
    .tail {
        transform: rotateY(-180deg);
    }
    :global(menu .flipHead) {
        animation: resultHead 1.2s ease-out forwards;
    }
    :global(menu .flipTail) {
        animation: resultTail 1.5s ease-out forwards;
    }

    @keyframes resultHead {
        from { transform: rotateY(0); }
        to { transform: rotateY(720deg); }
    }

    @keyframes resultTail {
        from { transform: rotateY(0); }
        to { transform: rotateY(900deg); }
    }
</style>
